   -webkit-align-items: center;
           align-items: center;
    width:100%;
    height:80px;
    background-color: #fff;
    box-shadow: inset 0 -1px 0 0 #dadada;
    position:relative;
}

.firstsingleStamp{
    width:100%;
    /* height:80px; */
   border-top:1px solid #dadada;
   /* box-shadow: inset 0 -1px 0 0 #dadada; */
}

